<template>


    <div class="box" :class="{'box-v': flagIsVertical}">
        
        <!-- 比例圖形 -->
        <div v-if="initRatio" class="graph" :style="{ 'aspect-ratio': initRatio }">
            <!-- 比例文字 -->
            <div class="text-wrap">{{ initRatio }}</div>
        </div>

        <!-- 輔助框線 & 比例文字 -->
        <span class="ratio-1-1"></span>
        <span class="ratio-2-1"></span>
        <span class="ratio-3-2"></span>
        <span class="ratio-4-3"></span>
        <span class="ratio-5-4"></span>
        <span class="ratio-16-9"></span>
        <span class="ratio-16-10"></span>
    </div>

</template>

<script>
import { toRef } from 'vue'

export default {
    name: 'RatioAspectGraph',
    props: ['ratio', 'isVertical'],

    setup(props) {
        // props
        let initRatio = toRef(props, 'ratio')
        let flagIsVertical = toRef(props, 'isVertical')


        return {
            initRatio,
            flagIsVertical
        }
    }
}
</script>