<template>

    <div class="container">
        <div class="row">
            <div class="col-12">

                <div class="ratio-aspect">
                    <h1>比例計算機</h1>

                    <div class="container">
                        <div class="row">
                            <div class="col-12 col-lg-6">
                                <div class="inner-wrap">
                                    <fieldset>
                                        <h5>寬度 (width): <input type="number" v-model="data.width" @input="getRatio"></h5>
                                        <h5>高度 (height): <input type="number" v-model="data.height" @input="getRatio"></h5>
                                        <span v-show="flag_zero" class="text-danger">請輸入正數</span>
                                    </fieldset>

                                    <fieldset>
                                        <h4>比例 (aspect-ratio):
                                            <span v-if="data.ratio" class="text-danger">
                                                {{ data.width_ratio }}:{{ data.height_ratio }}
                                                或
                                                {{ data.width_ratio * 2 }}:{{ data.height_ratio * 2 }}
                                            </span>
                                        </h4>
                                    </fieldset>

                                    <!-- 圖形 -->
                                    <RatioAspectGraph 
                                        :ratio="data.ratio" 
                                        :isVertical="data.isVertical" 
                                    />
                                </div>
                            </div>
                            <div class="col-12 col-lg-5">
                                <div class="inner-wrap">
                                    <fieldset>
                                        <h5>標準比例 (橫式)</h5>
                                        <table class="table table-striped">
                                            <thead>
                                                <tr>
                                                    <th>比例</th>
                                                    <th>寬度 {{ data.width }}</th>
                                                    <th>高度 {{ data.height }}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <th>2:1</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.5) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.5) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>16:9</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.5625) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.5625) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>16:10</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.625) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.625) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>3:2</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.6667) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.6667) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>4:3</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.75) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.75) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>5:4</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width*0.8) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height/0.8) }} x {{ data.height }}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </fieldset>

                                    <fieldset>
                                        <h5>標準比例 (直式)</h5>
                                        <table class="table table-striped">
                                            <thead>
                                                <tr>
                                                    <th>比例</th>
                                                    <th>寬度 {{ data.width }}</th>
                                                    <th>高度 {{ data.height }}</th>
                                                </tr>
                                            </thead>
                                            <tbody>
                                                <tr>
                                                    <th>1:2</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.5) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height*0.5) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>9:16</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.5625) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height*0.5625) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>10:16</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.625) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.width*0.625) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>2:3</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.6667) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height*0.6667) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>3:4</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.75) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height*0.75) }} x {{ data.height }}</td>
                                                </tr>
                                                <tr>
                                                    <th>4:5</th>
                                                    <td>{{ data.width }} x {{ roundTwoFloatDigits(data.width/0.8) }}</td>
                                                    <td>{{ roundTwoFloatDigits(data.height*0.8) }} x {{ data.height }}</td>
                                                </tr>
                                            </tbody>
                                        </table>
                                    </fieldset>
                                </div>
                            </div>
                        </div>
                    </div>

                </div>

            </div>
        </div>
    </div>
    
    

</template>

<script>
import { ref, reactive } from 'vue';
import RatioAspectGraph from './RatioAspectGraph.vue';
import { roundTwoFloatDigits } from '@/hooks/useFormatNumber';

export default {
    name: 'RatioAspect',
    components: {
        RatioAspectGraph
    },

    setup() {
        // 數據
        const data = reactive({
            width: 0,    // 寬度
            height: 0,   // 高度

            width_ratio: null,   // 寬比
            height_ratio: null,  // 高比

            ratio: null,         // 寬比/高比
            isVertical: false,   // 垂直圖形?
        })

        let flag_zero = ref(false)


        // 方法
        
        // 最大公因數
        //https://kim85326.github.io/2018/03/15/%E6%9C%80%E5%A4%A7%E5%85%AC%E5%9B%A0%E6%95%B8-%E6%9C%80%E5%B0%8F%E5%85%AC%E5%80%8D%E6%95%B8/
        function gcd(m, n) {
            var remainder = 0;
            do {
                remainder = m % n;
                m = n;
                n = remainder;
            } while (remainder !== 0);
            return m;
        }

        // 寬高比例
        let timer;
        function getRatio() {
            // 防抖
            clearTimeout(timer)
            timer = setTimeout(()=>{
                flag_zero.value = false

                // 檢查欄位值是否為0
                if ( data.width <=0 || data.height <=0 ) {
                    flag_zero.value = true
                    return
                }


                // 計算比例
                let gcd_value = gcd(data.width, data.height);
                data.width_ratio = data.width / gcd_value;
                data.height_ratio = data.height / gcd_value;

                data.ratio = `${data.width_ratio}/${data.height_ratio}`;
                data.isVertical = +data.width < +data.height ? true : false;
            }, 300)
        }

        

        return {
            data,
            flag_zero,

            getRatio,

            roundTwoFloatDigits,
        }
    }
}
</script>

